@-webkit-keyframes topRotation{
    0%{
        -webkit-transform-origin: center bottom;
                transform-origin: center bottom;
        -webkit-transform: scale(1,1) rotateX(0deg) perspective(800px);
                transform: scale(1,1) rotateX(0deg) perspective(800px);
        top: 0;
    }
    50%{

        -webkit-transform-origin: center bottom;

                transform-origin: center bottom;
        -webkit-transform: scale(1,1) rotateX(90deg) perspective(800px);
                transform: scale(1,1) rotateX(90deg) perspective(800px);
        top: 0;
    }
    50.001%{
        -webkit-transform-origin: center top;
                transform-origin: center top;
        -webkit-transform: scale(1,-1) rotateX(90deg) perspective(800px);
                transform: scale(1,-1) rotateX(90deg) perspective(800px);
        top: 50%;
    }
    100%{
        -webkit-transform-origin: center top;
                transform-origin: center top;
        -webkit-transform: scale(1,-1) rotateX(180deg) perspective(800px);
                transform: scale(1,-1) rotateX(180deg) perspective(800px);
        top: 50%;
    }
}

