@-webkit-keyframes switch2bottomHalf{
    0%{
        margin-top: 0%;
    }
    50%{
        margin-top: 0%;
    }
    50.001%{
        margin-top: -83%;
    }
    100%{
        margin-top: -83%;
    }
}

@keyframes switch2bottomHalf{
    0%{
        margin-top: 0%;
    }
    50%{
        margin-top: 0%;
    }
    50.001%{
        margin-top: -83%;
    }
    100%{
        margin-top: -83%;
    }
}

@-webkit-keyframes shading{
    0%{
       background-color: black;
       opacity: 0; 
    }
    50%{
        background-color: black;
        opacity: 0.5; 
    }
    50.001%{
        background-color: white;
        opacity: 0.5; 
    }
    100%{
        background-color: white;
        opacity: 0; 
    }
}

@keyframes shading{
    0%{
       background-color: black;
       opacity: 0; 
    }
    50%{
        background-color: black;
        opacity: 0.5; 
    }
    50.001%{
        background-color: white;
        opacity: 0.5; 
    }
    100%{
        background-color: white;
        opacity: 0; 
    }
}

.number-wrapper__individual_digit_main{
    width: 44px;
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    background-color: #4a2e1c;
    border-radius: 4px;
    padding-bottom: 5px;
    border-radius: 5px;;
    box-shadow: 2px 2px 3px rgba(34, 12, 12, 0.25);
}
.numbers--group__wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: center;
    margin-top: 4px;
}
.numbers--top{
    width: 100%;
    height: 50%;
    overflow: hidden;
    position: absolute;
    top: 0;
    background-color: #4a2e1c;
    border-bottom: 1px solid black;
    border-radius: 5px;
}
.numbers--top p{
    width: 100%;
    font-family:"franklin-gothic-urw-cond";
    font-size: 60px;
    font-weight: 700;
    text-align: center;
    color: white;
    background-color: #4a2e1c;
}
.updating .flipper{
    -webkit-animation-name: topRotation;
            animation-name: topRotation;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}
.updating .flipper.nextValue{
    -webkit-animation-name: topRotationNextNumber;
            animation-name: topRotationNextNumber;
}
.updating .flipper p{
    -webkit-animation-name: switch2bottomHalf;
            animation-name: switch2bottomHalf;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}
.updating .flipper::after{
    display: block;
    content:"";
    position: absolute;
    top:0;
    bottom: 0;
    left:0;
    right:0;
    -webkit-animation-name: shading;
            animation-name: shading;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}
.numbers--bottom{
    width: 100%;
    height: 100%;
}
.numbers--bottom p{
    font-family:"franklin-gothic-urw-cond";
    font-size: 60px;
    font-weight: 700;
    text-align: center;
    color: white;
    width: 100%;
}
